import React from 'react';
import PropTypes from 'prop-types';

const SortPopup = React.memo(function SortPopup({ items, activeSortType, onClickSortType }) {
  const [visiblePopup, setVisiblePopup] = React.useState(false);
  // const [activeItem, setActivItem] = React.useState(0);

  const sortRef = React.useRef(); //–ø—Ä–∏–≤—è–∑—ã–≤–∞–µ–º div sort —Ö—É–∫–æ–º
  // console.log(sortRef.current);
  // console.log('items', items);
  // console.log('activeSortType', activeSortType);

  const activeLabel = items.find((obj) => obj.type === activeSortType).name; //–±–µ—Ä–µ–º –∏–∑ –ø—Ä–æ–ø—Å–æ–≤ –∞–∫—Ç–∏–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç, –ø—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –µ–≥–æ –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π activeLabel –∏ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–µ –≤ –Ω—É–∂–Ω–æ–º span
  // const activeLabel = items.find((arr) => arr.type === activeSortType).name; //–±–µ—Ä–µ–º –∏–∑ –ø—Ä–æ–ø—Å–æ–≤ –∞–∫—Ç–∏–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç, –ø—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –µ–≥–æ –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π activeLabel –∏ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–µ –≤ –Ω—É–∂–Ω–æ–º span
  // const activeLabel = items[activeItem].name; //–±–µ—Ä–µ–º –∏–∑ –ø—Ä–æ–ø—Å–æ–≤ –∞–∫—Ç–∏–≤–Ω—ã–π —ç–ª–µ–º–µ–Ω—Ç, –ø—Ä–∏—Å–≤–∞–∏–≤–∞–µ–º –µ–≥–æ –ø–µ—Ä–µ–º–µ–Ω–Ω–æ–π activeLabel –∏ –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –µ–µ –≤ –Ω—É–∂–Ω–æ–º span
  // console.log('activeLabel', activeLabel);

  const toggleVisiblePopup = () => {
    setVisiblePopup(!visiblePopup);
  };
  //---
  const handleOutsideClick = (event) => {
    const path = event.path || (event.composedPath && event.composedPath()); //bug firefox
    // console.log(e.path); //–ø—É—Ç—å –∫ —ç–ª–µ–º–µ–Ω—Ç—É –Ω–∞ –∫–æ—Ç–æ—Ä–æ–º –±—ã–ª –∫–ª–∏–∫
    // –µ—Å–ª–∏ –≤ –ø—É—Ç–∏ –∫–ª–∏–∫–∞(includes -  –ø–æ–∏—Å–∫ –ø–æ –º–∞—Å—Å–∏–≤—É –ø—É—Ç–µ–π) –µ—Å—Ç—å –æ—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º—ã–π —Å–æ—Ä—Ç—Ä–µ—Ñ(–¥–∏–≤ —Å–æ—Ä—Ç)
    if (!path.includes(sortRef.current)) {
      setVisiblePopup(false); //–ø—Ä–∏ –∫–ª–∏–∫–µ –≤–Ω–µ –æ–±–ª–∞—Å—Ç–∏ –ø–æ–ø–∞–ø–∞, –ø–æ–ø–∞–ø —Å–∫—Ä—ã–≤–∞–µ—Ç—Å—è
      // console.log('outsidePopup');
    }
  };
  //–Ω–∞–≤–µ—à–∏–≤–∞–µ–º –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–ª–∏–∫–∞ –Ω–∞ –≤—Å–µ –æ–∫–Ω–æ
  React.useEffect(() => {
    document.body.addEventListener('click', handleOutsideClick);
  }, []);
  //---
  //–ø–µ—Ä–µ–∫–ª—é—á–∞—Ç–µ–ª—å –∞–∫—Ç–∏–≤–Ω–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞
  const onSelectItem = (index) => {
    if (onClickSortType) {
      onClickSortType(index);
    }
    // setActivItem(index);
    setVisiblePopup(false); //–ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –æ–¥–∏–Ω –∏–∑ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Å–ø–∏—Å–∫–∞ –ø–æ–ø–∞–ø–∞, –ø–æ–ø–∞–ø —Å–∫—Ä—ã–≤–∞–µ—Ç—Å—è
  };
  //---
  return (
    <div
      ref={sortRef}
      // ref={(ref) => {
      //   sortRef.current = ref;
      // }}
      className='sort'>
      <div className='sort__label'>
        <svg
          className={visiblePopup ? 'rotated' : ''}
          width='10'
          height='6'
          viewBox='0 0 10 6'
          fill='none'
          xmlns='http://www.w3.org/2000/svg'>
          <path
            d='M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z'
            fill='#2C2C2C'
          />
        </svg>
        <b>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</b>
        <span onClick={toggleVisiblePopup}>{activeLabel}</span>
      </div>
      {visiblePopup && (
        <div className='sort__popup'>
          <ul>
            {items?.map((obj, index) => (
              <li
                onClick={() => onSelectItem(obj)}
                // onClick={() => onSelectItem(obj.type)}
                className={activeSortType === obj.type ? 'active' : ''}
                key={`${obj.type}_${index}`}>
                {obj.name}
              </li>
            ))}
            {/* <li className='active'>–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏</li>
          <li>—Ü–µ–Ω–µ</li>
          <li>–∞–ª—Ñ–∞–≤–∏—Ç—É</li> */}
          </ul>
        </div>
      )}
    </div>
  );
});
//rfce

//–ø—Ä–æ–≤–µ—Ä–∫–∞ —Ç–∏–ø–æ–≤ –ø—Ä–æ–ø—Å–æ–≤
SortPopup.propTypes = {
  activeSortType: PropTypes.string.isRequired,
  items: PropTypes.arrayOf(PropTypes.object).isRequired,
  onClickSortType: PropTypes.func.isRequired,
};

SortPopup.defaultProps = { items: [] };

export default SortPopup;
