import React from 'react';
import { Categories, SortPopup, PizzaBlock, PizzaLoadingBlock } from './../components';
import { useSelector, useDispatch } from 'react-redux';
import { setCategory, setSortBy } from '../redux/reducers/filters';
// import { onAddCart } from '../redux/reducers/cart';//test
import { fetchPizzas } from '../redux/reducers/pizzas';
import { addPizzaToCart } from '../redux/reducers/cart';

function Home() {
  const dispatch = useDispatch();
  const { items, isLoaded } = useSelector(({ pizzas }) => pizzas);
  const { category, sortBy } = useSelector(({ filters }) => filters);
  const cartItems = useSelector(({ cart }) => cart.items);
  // console.log(cartItems);
  const categoryNames = ['–ú—è—Å–Ω—ã–µ', '–í–µ–≥–∞—Ç–∞—Ä–∏–∞–Ω—Å–∫–∞—è', '–ì—Ä–∏–ª—å', '–û—Å—Ç—Ä—ã–µ', '–ó–∞–∫—Ä—ã—Ç—ã–µ'];
  const sortItems = [
    { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'rating', order: 'desc' },
    // { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', type: 'popular', order: 'desc' },
    { name: '—Ü–µ–Ω–µ', type: 'price', order: 'desc' },
    { name: '–∞–ª—Ñ–∞–≤–∏—Ç—É', type: 'name', order: 'asc' },
  ];

  React.useEffect(() => {
    dispatch(fetchPizzas(category, sortBy));
  }, [category, sortBy, dispatch]);

  const onSelectCategory = React.useCallback(
    (index) => {
      dispatch(setCategory(index));
    },
    [dispatch],
  );

  const onSelectSortType = React.useCallback(
    (obj) => {
      dispatch(setSortBy(obj));
    },
    [dispatch],
  );

  const handleAddPizzaToCart = (obj) => {
    dispatch(addPizzaToCart(obj));
  };

  return (
    <div className='container'>
      <div className='content__top'>
        <Categories
          activeCategory={category}
          onClickCategory={onSelectCategory}
          // onClickItem={(index) => dispatch(setCategory(index))}
          items={categoryNames}
        />
        <SortPopup
          // activeSortType={sortBy}
          activeSortType={sortBy.type}
          items={sortItems}
          onClickSortType={onSelectSortType}
        />
        {/* <SortPopup items={sortItems} /> */}
      </div>
      <h2 className='content__title'>–í—Å–µ –ø–∏—Ü—Ü—ã</h2>
      <div className='content__items'>
        {isLoaded
          ? items.map((obj) => (
              <PizzaBlock
                onClickAddPizza={handleAddPizzaToCart}
                addedCount={cartItems[obj.id] && cartItems[obj.id].items.length}
                key={obj.id}
                {...obj} //–æ–ø–µ—Ä–∞—Ç–æ—Ä —Ä–∞—Å–ø—Ä–æ—Å—Ç—Ä–∞–Ω–µ–Ω–∏—è spread (‚Ä¶) –ø—Ä–æ–∫–∏–¥—ã–≤–∞–µ–º –≤—Å–µ —Å–≤–æ–π—Å—Ç–≤–∞ –æ–±—ä–µ–∫—Ç–∞ –≤ –ø—Ä–æ–ø—Å—ã
              />
            )) //—Ö–∞—Ä–¥–∫–æ–¥–∏–º –º–∞—Å—Å–∏–≤ 10—Ç–∏ –∑–∞–≥–ª—É—à–µ–∫(—Ç–∞–∫ —Å–µ–±–µ —Ä–µ—à–µ–Ω–∏–µ)
          : Array(10)
              .fill(0)
              .map((_, i) => <PizzaLoadingBlock key={i} />)}
      </div>
    </div>
  );
}

export default Home;
