import React from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import Button from './Button';

function PizzaBlock({ id, name, imageUrl, sizes, price, types, onClickAddPizza, addedCount }) {
  const availableTypes = ['—Ç–æ–Ω–∫–æ–µ', '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ'];
  const availableSizes = [26, 30, 40];

  const [activeType, setActivType] = React.useState(types[0]);
  const onSelectType = (index) => {
    setActivType(index);
  };

  const [activeSize, setActivSize] = React.useState(0);
  const onSelectSize = (index) => {
    setActivSize(index);
  };

  const onAddPizza = () => {
    const obj = {
      id,
      name,
      imageUrl,
      price,
      size: availableSizes[activeSize],
      type: availableTypes[activeType],
    };
    onClickAddPizza(obj);
  };

  return (
    <div className='pizza-block'>
      <img className='pizza-block__image' src={imageUrl} alt='Pizza' />
      <h4 className='pizza-block__title'>{name}</h4>
      <div className='pizza-block__selector'>
        <ul>
          {availableTypes.map((type, index) => (
            <li
              onClick={() => onSelectType(index)}
              //–≤ –æ–±—ä–µ–∫—Ç–µ –∫–ª—é—á –∏–º—è –∫–ª–∞—Å—Å–∞, –∑–Ω–∞—á–µ–Ω–∏–µ —É—Å–ª–æ–≤–∏–µ –ø—Ä–∏ –∫–æ—Ç–æ—Ä–æ–º –∫–ª–∞—Å—Å –Ω–∞–∑–Ω–∞—á–∞–µ—Ç—Å—è
              className={classNames({
                active: activeType === index,
                disable: !types.includes(index),
              })}
              // className={activeType === index ? 'active' : ''}
              key={`${type}_${index}`}>
              {type}
            </li>
          ))}
          {/* <li className={activeType === 0 ? 'active' : ''}>—Ç–æ–Ω–∫–æ–µ</li>
          <li className={activeType === 1 ? 'active' : ''}>—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ</li> */}
        </ul>
        <ul>
          {availableSizes.map((size, index) => (
            <li
              className={classNames({
                active: activeSize === index,
                disable: !sizes.includes(size),
              })}
              onClick={() => onSelectSize(index)}
              key={`${size}_${index}`}>
              {size} —Å–º.
            </li>
          ))}
        </ul>
      </div>
      <div className='pizza-block__bottom'>
        <div className='pizza-block__price'>–æ—Ç {price} ‚ÇΩ</div>
        <Button
          onClick={onAddPizza}
          // onClick={() => onClickAddPizza({ id, name, imageUrl, sizes, price })}
          className='button--add'
          outline>
          <svg
            width='12'
            height='12'
            viewBox='0 0 12 12'
            fill='none'
            xmlns='http://www.w3.org/2000/svg'>
            <path
              d='M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z'
              fill='white'
            />
          </svg>
          <span>–î–æ–±–∞–≤–∏—Ç—å</span>
          {addedCount && <i>{addedCount}</i>}
        </Button>
      </div>
    </div>
  );
}

//name, imageUrl, sizes, price, types//–ø—Ä–æ–≤–µ—Ä–∫–∞ —Ç–∏–ø–æ–≤ –ø—Ä–æ–ø—Å–æ–≤
PizzaBlock.propTypes = {
  name: PropTypes.string,
  imageUrl: PropTypes.string,
  price: PropTypes.number,
  types: PropTypes.arrayOf(PropTypes.number).isRequired,
  sizes: PropTypes.arrayOf(PropTypes.number).isRequired,
  onAddPizza: PropTypes.func,
  addedCount: PropTypes.number,
};

PizzaBlock.defaultProps = {
  name: '---',
  price: 0,
  types: [],
  sizes: [],
  // addedCount: 0,
};

export default PizzaBlock;
